import React, { useState } from 'react';
import './App.css';
import data from './data';
import List from './list';


function App() {
  const [people, setPeople] = useState(data);
  const removePerson = (id) => {
    const newPerson = people.filter((people)=>people.id !== id);
    setPeople(newPerson)
  }
  return (
    <main>
      
        <h1 className="title">Birthday Reminder Project</h1>
        <section className='container'>
          <h3><span>{people.length}</span> birthdays today</h3>
          <List people={people} removePerson={removePerson} />
          <button className="remove" onClick={()=>setPeople([])}>Clear all</button>
        </section>
        <p className="name">By MOHAMMED NAJJAR</p>
        
    </main>
  );
}

export default App;
